import * as ReactNativeFeatureFlags from '../../src/private/featureflags/ReactNativeFeatureFlags';
const SUPPORTED_COLOR_STYLES   
                   
  = {
  backgroundColor: true,
  borderBottomColor: true,
  borderColor: true,
  borderEndColor: true,
  borderLeftColor: true,
  borderRightColor: true,
  borderStartColor: true,
  borderTopColor: true,
  color: true,
  tintColor: true
};
const SUPPORTED_STYLES   
                   
  = {
  ...SUPPORTED_COLOR_STYLES,
  borderBottomEndRadius: true,
  borderBottomLeftRadius: true,
  borderBottomRightRadius: true,
  borderBottomStartRadius: true,
  borderEndEndRadius: true,
  borderEndStartRadius: true,
  borderRadius: true,
  borderTopEndRadius: true,
  borderTopLeftRadius: true,
  borderTopRightRadius: true,
  borderTopStartRadius: true,
  borderStartEndRadius: true,
  borderStartStartRadius: true,
  elevation: true,
  opacity: true,
  transform: true,
  zIndex: true,
  shadowOpacity: true,
  shadowRadius: true,
  scaleX: true,
  scaleY: true,
  translateX: true,
  translateY: true
};
const SUPPORTED_TRANSFORMS   
                   
  = {
  translateX: true,
  translateY: true,
  scale: true,
  scaleX: true,
  scaleY: true,
  rotate: true,
  rotateX: true,
  rotateY: true,
  rotateZ: true,
  perspective: true,
  skewX: true,
  skewY: true,
  matrix: ReactNativeFeatureFlags.shouldUseAnimatedObjectForTransform()
};
const SUPPORTED_INTERPOLATION_PARAMS   
                   
  = {
  inputRange: true,
  outputRange: true,
  extrapolate: true,
  extrapolateRight: true,
  extrapolateLeft: true
};
export function allowInterpolationParam(param        )       {
  SUPPORTED_INTERPOLATION_PARAMS[param] = true;
}
export function allowStyleProp(prop        )       {
  SUPPORTED_STYLES[prop] = true;
}
export function allowTransformProp(prop        )       {
  SUPPORTED_TRANSFORMS[prop] = true;
}
export function isSupportedColorStyleProp(prop        )          {
  return SUPPORTED_COLOR_STYLES[prop] === true;
}
export function isSupportedInterpolationParam(param        )          {
  return SUPPORTED_INTERPOLATION_PARAMS[param] === true;
}
export function isSupportedStyleProp(prop        )          {
  return SUPPORTED_STYLES[prop] === true;
}
export function isSupportedTransformProp(prop        )          {
  return SUPPORTED_TRANSFORMS[prop] === true;
}