const objectFitMap                         = {
  contain: 'contain',
  cover: 'cover',
  fill: 'stretch',
  'scale-down': 'contain',
};

export function convertObjectFitToResizeMode(objectFit         )              {
  return objectFit != null ? objectFitMap[objectFit] : undefined;
}
