                                                                                                       
import { isSupportedInterpolationParam, isSupportedStyleProp, isSupportedTransformProp } from '../../../Libraries/Animated/NativeAnimatedAllowlist';
export function validateInterpolation                          (config                                  )       {
  for (const key in config) {
    if (!isSupportedInterpolationParam(key)) {
      console.error(`Interpolation property '${key}' is not supported by native animated module`);
    }
  }
}
export function validateStyles(styles   
                         
     
 )       {
  for (const key in styles) {
    if (!isSupportedStyleProp(key)) {
      console.error(`Style property '${key}' is not supported by native animated module`);
    }
  }
}
export function validateTransform(configs         
                   
                   
                   
     
     
                 
                   
                         
     
  )       {
  configs.forEach(config => {
    if (!isSupportedTransformProp(config.property)) {
      console.error(`Property '${config.property}' is not supported by native animated module`);
    }
  });
}